export const forEachUTF32=(str,cb)=>{
    let i=0;
    while (i<str.length) {
        const code=str.codePointAt(i)||0;
        const ch=String.fromCodePoint(code);
        cb(ch,i,str);
        i++;
        if (code>0xffff) i++;
    }
}
export const substrUTF32=(str,from, n)=>{
    if (!str || !n || n<0) return '';
    let i=from;
    while (n>0 && i<str.length) {
        if (str.codePointAt(i)>0xffff) {
            i++;
        }
        n--;
        i++;
    }
    return str.slice(from,i);
}
export const splitUTF32=(str)=>{
    if (!str) {
        const empty=[];
        return empty
    }
    let i=0;
    const out=[];
    while (i<str.length) {
        const code=str.codePointAt(i)||0;
        out.push(code);
        i++;
        if (code>0xffff) i++;
    }
    return out;
}
export const splitUTF32Char=(str)=>splitUTF32(str).map( cp=>String.fromCodePoint(cp));
export const codePointLength=(str)=>splitUTF32(str).length;
export const StringByteLength=(str)=>new Blob([str]).size;
export const UnicodeBlock=(n)=>{
    if (!n) return '';
    const cp=(typeof n=='string') ?n.codePointAt(0):n;
    if (cp<0x80) return ' ascii';
    else if (cp<0x400) return '2 2b Latin';
    else if (cp<0x900) return ' Miscellaneous';
    else if (cp<0xd80) return ' Indic scripts';
    else if (cp<0xdf0) return 'Sinhala';
    else if (cp<0xe80) return ' Thai';
    else if (cp<0xf00) return ' Lao';
    else if (cp<0x1000) return ' Tibet';
    else if (cp<0x1100) return ' Myanmar';
    else if (cp<0x1200) return ' Korean';
    else if (cp<0x1780) return ' Miscellaneous';
    else if (cp<0x1800) return ' Khemar';
    else if (cp<0x18D0) return ' Mongolian';
    else if (cp<0x2000) return ' Alphabets';
    else if (cp<0x2060) return ' Puncuations';
    else if (cp<0x2E80) return ' Miscellaneous';
    else if (cp<0x2FF0) return ' Radical';
    else if (cp<0x3000) return ' IDC';
    else if (cp<0x3040) return ' Japanese Puncuation';
    else if (cp<0x3100) return ' Japanese';
    else if (cp<0x3140) return ' Bopomofo';
    else if (cp<0x31D0) return ' Korean';
    else if (cp<0x31F0) return ' Strokes';
    else if (cp<0x3400) return ' Kisyu-izon-moji';
    else if (cp<0x4E00) return 'A';
    else if (cp<0xA000) return '';
    else if (cp<0xA4D0) return ' Yi';
    else if (cp<0xAC00) return ' Miscellaneous';
    else if (cp<0xE000) return ' Korean';
    else if (cp<0xFAE0) return ' Private Use Area';
    else if (cp<0xFF00) return ' Miscellaneous';
    else if (cp<0x10000) return  ' Full Width';
    else if (cp<0x20000) return ' Miscellaneous';
    else if (cp<0x2A7D0) return 'B';
    else if (cp<0x2B7A0) return 'C';
    else if (cp<0x2B880) return 'D';
    else if (cp<0x2CF10) return 'E';
    else if (cp<0x2FFFF) return 'F';
    else if (cp<0x40000) return 'G';
}