const fontfacedef={};

export const addFontFace=(name,settings)=>{
	fontfacedef[name]=settings;
}

export const getFontFace=(name)=>{
	return fontfacedef[name];
}
export const enumFontFace=()=>{
	return Object.keys(fontfacedef);
}

addFontFace('', { kMinWidthY:2, kMinWidthU:2, kMinWidthT:4.5, kWidth:5})
addFontFace('',{ kMinWidthY:2, kMinWidthU:1, kMinWidthT:3, kWidth:5})
addFontFace('',{ kMinWidthY:2, kMinWidthU:2, kMinWidthT:6, kWidth:5 })
addFontFace('',{ kMinWidthY:2.5, kMinWidthU:2, kMinWidthT:7, kWidth:5 })
addFontFace('',{ kMinWidthY:3, kMinWidthU:2, kMinWidthT:8, kWidth:5})

addFontFace('',{  hei:true, kWidth:2 })
addFontFace('',{ hei:true, kWidth:1 })
addFontFace('',{ hei:true, kWidth:3  })
addFontFace('',{ hei:true, kWidth:5})
addFontFace('',{ hei:true, kWidth:7})

export default {getFontFace,enumFontFace,addFontFace}