
<script>
import {onMount} from 'svelte'
import {codePointLength} from 'ptk/utils/unicode.ts'
import Hzpx,{splitPinx,drawPinx, derivedOf, enumFontFace ,getLastComps,gid2ch,isDataReady} from 'hzpx-engine';
import Glyph from './glyph.svelte'
import TestBench from './testbench.svelte';
import {downloadSvg} from './svg2png.js'
import Favorite from './favorite.svelte'
//if (window.location.protocol==='https:') registerServiceWorker();

Window.Hzpx=Hzpx;
let value='' //';//'//' //// //
let ready=false;
let showhelp=false;
let message='';
// document.title="-"+glyphWikiCount();
let timer;
onMount(async ()=>{
	timer=setInterval(function(){
		if (isDataReady()) {
			setTimeout(function(){
				value='';
				ready=true;
			},2000);//wait for data ready
			clearInterval(timer);
		}
	},1000);
})
let svgs=[], frame=false , showfont=false, showinfo=false , size=200, fontface='' ;
let testbench=false;

$: svgs        = ready?drawPinx(value,{size,fontface,frame}):[]; //allow mix normal char and pinxing expression
$: pinxUnits   = splitPinx(value,true);

$: components  = [];//getRenderComps(value)||[];
$: fontfaces   = enumFontFace();
$: replacables = getLastComps(value);
$: derives = (showinfo && codePointLength(value)==1 && derivedOf(value,200) ) ||[];
const toPNG=e=>downloadSvg(e.target,value+".png",size);
const focusInput=()=>{
	const input=document.querySelector('.input');
	input.focus();
	input.selLength=value.length;
}
const replaceComp=(comp)=>{ value+=comp+''; focusInput()};
const setBase=gid=>value=gid2ch(gid);
//why  cannot ?
//bug  cannot replace moon
/* to fix
// u248e9 wrong 
*/
const copylink=()=>{
	const url="https://nissaya.cn/hzpx?g="+value;
	navigator.clipboard.writeText(url);
	message=''
	setTimeout(()=>{
		message='';
	},2000)
}
</script>
<!-- svelte-ignore a11y-no-static-element-interactions -->
<div class="container">
<!-- svelte-ignore a11y-click-events-have-key-events -->
{#if ready}<span class=clickable on:click={()=>testbench=!testbench}></span>{/if}
{#if testbench}
<TestBench {fontface}/>
{:else}

{#if ready}
<input class="input" maxlength ="25" size="14" bind:value placeholder="" /><button on:click={copylink}></button>
<br/>
{#if message}{message}
{:else}
<Favorite bind:value/>
{/if}
<!-- svelte-ignore a11y-no-static-element-interactions -->
<!-- svelte-ignore a11y-click-events-have-key-events -->
<span title="Frame " class:selected={frame} on:click={()=>frame=!frame}></span>
<!-- svelte-ignore a11y-click-events-have-key-events -->
<span title="Font " class=clickable class:selected={showfont} on:click={()=>showfont=!showfont}></span>
{#if showfont}
{#each fontfaces as ff}
<!-- svelte-ignore a11y-no-static-element-interactions -->
<!-- svelte-ignore a11y-click-events-have-key-events -->
<span class=clickable class:selected={ff==fontface} on:click={()=>fontface=ff}>{ff} </span> 
{/each}
{/if}
<br/>
{#each svgs as svg,idx}
<!-- svelte-ignore a11y-no-static-element-interactions -->
<!-- svelte-ignore a11y-click-events-have-key-events -->

<span title={pinxUnits[idx]} on:click={toPNG}>{@html svg}</span>
{/each}
{#each replacables as comp}
<!-- svelte-ignore a11y-no-static-element-interactions -->
<!-- svelte-ignore a11y-click-events-have-key-events -->

<span class="replacecomp" on:click={()=>replaceComp(comp)}>{comp}</span>
{/each}
<br/>

<!-- svelte-ignore a11y-no-static-element-interactions -->
<!-- svelte-ignore a11y-click-events-have-key-events -->
{#if value.length<2}
<span title="Members and Derived " class:selected={showinfo} on:click={()=>showinfo=!showinfo}></span>
{/if}
{#key value}
{#if showinfo}
{#each derives as gid}
<Glyph {gid} {fontface} onclick={()=>setBase(gid)}/>
{/each}
{#each components as gid}
<br/><Glyph {gid} derivable={true} {fontface} />
{/each}




{:else}
<!-- svelte-ignore a11y-click-events-have-key-events -->
<!-- svelte-ignore a11y-missing-attribute -->
<a on:click={()=>showhelp=!showhelp} ></a>
{#if showhelp}
<h3></h3>
1,  () 
<br/>2/ ,  ,  , 
<br/>3(PNG) 
<br/>4
<br/>5

<h3></h3>

<br/>"//" 
<br/>
<br/>
<br/>Office 365  Google SpreadSheet  =IMAGE() 
<h3></h3>
html+js 
<br/>Unicode A-G 
<br/>ISCGPL
<br/>A. Glyphwiki.org    B. Kage()  
<h3></h3>
1.Glyphwiki
<br/>2.Glyphwiki 
<br/>3.glyphwiki
<br/>4.40BCJK2.5MB~3MB16x16
<br/>5.
<br/> Sukhanika ,  Gmail : yapcheahshen
{/if}
{/if}
{/key}
{:else}
<img src="hzpx.png" alt=""/>
<br/>()
<br/>System Loadingin 10 seconds

{/if}
{/if}
</div>